.footer__modal {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 768px;
  border-radius: 15px;
  background: var(--accent-color);
  text-align: center;
  z-index: 1000;
  box-shadow: 0 0 5px 5px rgba(0,0,0,0.2);
  &.active {
    display: block;
  }
  &-wrapper {
    position: relative;
    padding: 58px 20px 60px;
  }
  &-title {
    font-size: 20px;
    line-height: 140%; /* 28px */
    letter-spacing: -1px;
    text-transform: uppercase;
    margin-bottom: 30px;
  }
  p {
    font-family: "Alegreya Sans";
    font-size: 16px;
    line-height: 130%;
  }
  &-subtitle {
    font-family: "Alegreya Sans";
    font-size: 16px;
    font-weight: 700;
    line-height: 130%; /* 20.8px */
  }
  &-delivery {
    margin-bottom: 20px;
  }
  &-btn {
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
  }
}


@media (max-width: 900px) {
  .footer__modal {
    max-width: 100%;
    width: 90%;
  }
}
